function formatNumber (n) {
  const str = n.toString()
  return str[1] ? str : `0${str}`
}

export function formatTime (date) {
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()

  const hour = date.getHours()
  const minute = date.getMinutes()
  const second = date.getSeconds()

  const t1 = [year, month, day].map(formatNumber).join('/')
  const t2 = [hour, minute, second].map(formatNumber).join(':')

  return `${t1} ${t2}`
}

// 
export function passTime (time) {
  const now = Date.now()
  const t = (now - time) / 1000 / 60// 
  let result
  if (t / 60 / 24 / 30 / 12 > 1) {
    result = `${Math.floor(t / 60 / 24 / 30 / 12)}`
    return result
  } else if (t / 60 / 24 / 30 > 1) {
    result = `${Math.floor(t / 60 / 24 / 30)}`
    return result
  } else if (t / 60 / 24 > 1) {
    result = `${Math.floor(t / 60 / 24)}`
    return result
  } else if (t / 60 > 1) {
    // 
    result = `${Math.floor(t / 60)}`
    return result
  } else {
    // 
    result = `${Math.floor(t)}`
    return result
  }
}

// 
export function getURL () {
  return getCurrentPages().slice(-1)[0].route
}

export function debounce (fn, interval = 300) {
  let timeout = null
  return function () {
    clearTimeout(timeout)
    timeout = setTimeout(() => {
      fn.apply(this, arguments)
    }, interval)
  }
}
